export default {
  name: "admin",
  components: {},
  data: () => ({
    timestamp: 0,
    prevSelectedIds: [],
    selected: [],
    date1: "",
    headers: [
      {
        text: "کد راننده",
        align: "start",
        value: "DriverCode",
        sortable: false
      },
      {
        text: "فاکتور",
        align: "start",
        value: "Number",
        sortable: false
      },
      { text: "نام‌فروشگاه", value: "ShopName",sortable: false },
      { text: "دسته‌بندی", value: "way",sortable: false },
      { text: "محصولات", value: "Items", sortable: false },
      { text: "وضعیت", value: "Status",sortable: false },
      { text: "توضیحات تکمیلی", value: "Description",sortable: false },
    ],
    boxes: [],
    filterBoxes: [],
    localeConfig: {
      fa: {
        displayFormat: "jYYYY/jMM/jDD",
        lang: { label: "شمسی" },
      },
    },
    show: true,
    categories: ["dispatch", "dispatch2", "cookie", "bread-1", "bread-2"],
    category: "",
    loading: true,
    typesArray: ["کد راننده", "نام فروشگاه", "وضعیت", "دسته‌بندی"],
