  watch: {
    date1() {
      this.getAllOrders(new Date(this.date1));
    },
  },
  computed: {
    minDate() {
      let d = new Date();
      d.setDate(d.getDate() - 3);
      return d.toISOString().split("T")[0];
    },
    maxDate() {
      let d = new Date();
      d.setDate(d.getDate() + 3);
      return d.toISOString().split("T")[0];
    },
  },
  mounted() {
    // مقدار اولیه prev خالیه (یا اگر میخوای از سرور مقدار گرفتنی باشه اینجا update کن)
    this.prevSelectedIds = [];
